export default {
  name: "rechargeList",
  data() {
      return {
          payList: [],
          page:1,
          noData:false,
      }
  },
  onReachBottom(){
      if(this.noData){
          return uni.showToast({title: '暂无更多数据', icon: 'none'});
      }
      this.page+=1
      this.getpayLog()
  },
  onLoad(options) { 
      this.getpayLog()
  },
  methods: {
      async getpayLog (){
          uni.showLoading({mask: true, title: '加载中...',})
          try{
              let info = await this.$request({
                  url: this.$api.myrecharge.payLog,
                  data: {page: this.page}
              })
              let { code, data, msg } = info;
              if (data.length) {
                  this.payList = [...this.payList, ...data]
              } else {
                  this.noData = true
              }
          }catch(e){}
          uni.hideLoading();
      }
  }
}
